import $ from "jquery";

const franc_mobile_money = async (ref, name, email, number, amount, country, _callback) => {
    let currency = "XOF";
    if (country === "CM") {
        currency = "XAF"
    }
    let d = {
        "pub_key": process.env.FLW_PUBLIC_KEY,
        "sec_key": process.env.FLW_SECRET_KEY,
        "fullname": name,
        "email": email,
        "number": number,
        "tx_ref": ref,
        "country": country,
        "currency": currency,
        "amount": amount,
    }
    try {
        $.ajax({
            type: "GET",
            url: "https://flutterwave-nodejs.onrender.com/api/collect",
            data: d,
            success(data) {
                _callback(data);
            },
        });
    } catch (error) {
        console.log(error)
    }
}

